.title {
	font-size: 23px;
	font-weight: 700;
}

.daily-item {
	background-color: #f5f5f5;
	border-radius: 15px;
	height: 40px;
	margin: 5px;
	display: flex;
	align-items: center;
	cursor: pointer;
	font-size: 14px;
	padding: 5px 20px;
    box-shadow: 10px -2px 20px 2px rgba(0, 0, 0, 30%);
}

.icon-small {
	width: 40px;
}

.day {
	color: #212121;
	flex: 1 1;
	font-weight: 600;
	margin-left: 15px;
}

.description {
	flex: 1 1;
	margin-right: 15px;
	text-align: right;
	text-transform: capitalize;
}

.min-max {
    color: #757575;
}

.daily-details-grid {
    display: grid;
    grid-row-gap: 0;
    grid-column-gap: 15px;
    column-gap: 15px;
    row-gap: 0;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
    background-color: #333;
    color: #f5f5f5;
    border-radius: 15px;
    margin: 5px;
    box-shadow: 10px -2px 20px 2px rgba(0, 0, 0, 30%);
}

.daily-details-grid-item {
    align-items: center;
    display: flex;
    height: 30px;
    justify-content: space-between;
}